  margin-left: 0.5em;
  margin-right: 0.5em;
  font-size: 2.1vmin;
}
.navbar-dark .navbar-toggler {
  border-color: transparent;
}
.searchBar{
    position: absolute;
    z-index: 2;
    display: block;
    width: 2.375rem;
    height: 2.375rem;
    line-height: 2.375rem;
    text-align: center;
    pointer-events: none;
    color: #aaa;
}
.addPadding{
    padding-left: 2.375rem;
}

